// ALS Hauss
@font-face {
  font-family: "ALS Hauss";
  src: url("../fonts/ALS Hauss/ALSHauss-Bold.eot");
  src: local("ALS Hauss Bold"), local("ALSHauss-Bold"),
    url("../fonts/ALS Hauss/ALSHauss-Bold.eot?#iefix")
      format("embedded-opentype"),
    url("../fonts/ALS Hauss/ALSHauss-Bold.woff2") format("woff2"),
    url("../fonts/ALS Hauss/ALSHauss-Bold.woff") format("woff"),
    url("../fonts/ALS Hauss/ALSHauss-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

